.game {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100%;
  width: 100%;
  background: #000;
}

.wrapper {
  position: relative;
  /* width: 900px;
  height: 600px; */
  background-color: #fafafa;
}

.chartWrapper {
  width: 100%;
  height: 250px;
}

@media (min-width: 700px) {
  .game:not(.isMobile) .wrapper {
    overflow: hidden;
    border-radius: 6px;
  }
}
